body {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: #333;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
  }
  
  a {
    color: #007bff;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
    overflow: hidden;
  }
  
  header {
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  header h1 {
    margin: 0;
    font-size: 2rem;
    font-weight: bold;
    color: #333;
  }
  
  header nav {
    display: flex;
    align-items: center;
  }
  
  header nav a {
    margin-left: 20px;
    padding: 5px;
    border-radius: 5px;
    background-color: #ddd;
    color: #333;
    font-size: 0.9rem;
    transition: background-color 0.3s ease-in-out;
  }
  
  header nav a:hover {
    background-color: #eee;
  }
  
  main {
    padding: 20px;
  }
  
  .show-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 10px;
  }
  
  .show-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease-in-out;
  }
  
  .show-card:hover {
    transform: translateY(-5px);
  }
  
  .show-card img {
    width: 100%;
    height: auto;
    margin-bottom: 10px;
    border-radius: 5px 5px 0 0;
  }
  
  .show-card h3 {
    margin: 0;
    margin-bottom: 5px;
    font-size: 1.2rem;
    font-weight: bold;
    color: #333;
  }
  
  .show-card p {
    margin: 0;
    margin-bottom: 5px;
    color: #666;
  }
  
  .show-card .genres {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
  }
  
  .show-card .genres span {
    margin: 0 5px;
    padding: 2px 5px;
    border-radius: 10px;
    background-color: #ddd;
    font-size: 0.8rem;
    color: #333;
  }
  
  .show-card .rating {
    margin: 0;
    margin-bottom: 5px;
    font-size: 0.9rem;
    color: #f00;
  }
  
  .show-card .status {
    margin: 0;
    margin-bottom: 10px;
    font-size: 0.8rem;
    color: #666;
  }
  
